  font-size: 20px;
}
.box-right .doing ul li div:last-child {
  width: 70%;
  border-bottom: 1px solid #f1f1f1;
  font-size: 0;
  position: relative;
  box-shadow: 0 1px 1px #999;
}
.box-right .doing ul li div:last-child::before {
  content: "";
  width: 70%;
  border-bottom: 2px solid #0abe88;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  animation: aa 2s ease-out forwards;
  -webkit-animation: aa 2s ease-out forwards;
  transform-origin: left;
}
@-webkit-keyframes aa {
  100% {
    transform: scaleX(1);
    -webkit-transform: scaleX(1);
    -moz-transform: scaleX(1);
    -ms-transform: scaleX(1);
    -o-transform: scaleX(1);
  }
}
@keyframes aa {
  100% {
    transform: scaleX(1);
    -webkit-transform: scaleX(1);
    -moz-transform: scaleX(1);
    -ms-transform: scaleX(1);
    -o-transform: scaleX(1);
  }
}
.box-right .doing ul li:nth-child(2) div:last-child::before {
  width: 80%;
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}
.box-right .doing ul li:nth-child(3) div:last-child::before {
  width: 74%;
  -webkit-animation-delay: 1.4s;
          animation-delay: 1.4s;
}
.box-right .doing ul li:nth-child(4) div:last-child::before {
  width: 60%;
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
}
.box-right .doing ul li:nth-child(5) div:last-child::before {
  width: 70%;
  -webkit-animation-delay: 1.8s;
          animation-delay: 1.8s;
}
.box-right .doing ul li:nth-child(6) div:last-child::before {
  width: 65%;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

.box-right #about > h3 {
