    top: -40px;
  }
}

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  outline: none;
}

body {
  min-height: 100vh;
  background: linear-gradient(159deg, #4e4eeb, #bb4ab3);
  font-family: "Roboto", sans-serif;
}

#wrapper {
  width: 900px;
  height: 90vh;
  min-height: 80vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
}

.box-left {
  width: 30%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  padding: 2rem 0;
}
.box-right {
  width: 67%;
  height: 100%;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.box-left .card-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.box-left .item-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.box-left .item-1 > h2 {
  font-size: 1.2rem;
  font-weight: 500;
  padding: 10px;
  margin: 5px 0;
  color: rgb(71, 69, 69);
}
.box-left .item-1 > button {
  border: none;
  font-size: 1rem;
  padding: 7px 20px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  position: relative;
  overflow: hidden;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
  background: rgba(255, 72, 0, 0.342);
  color: #eee;
}
.box-left .item-1 .my-links1 {
  display: flex;
  margin: 20px 10px;
}
.box-left .item-1 .my-links1 a {
  color: #444;
  font-size: 23px;
  margin: 0 7px;
  display: inline-block;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.box-left .item-1 .my-links1 a:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
